
import {useState} from "react";

export const STATE = {
    'OPENING': 'OPENING',
    'OPEN': 'OPEN',
    'CLOSING': 'CLOSING',
    'CLOSED': 'CLOSED',
};

export const useDisclosureWithAnimation = (animateTime = 250) => {
    const [state, setState] = useState(STATE.CLOSED);

    const onOpen = () => {
        if (state === STATE.CLOSED) {
            setState(STATE.OPENING);
            setTimeout(() => {
                setState(STATE.OPEN);
            }, animateTime);
        }
    };

    const onClose = () => {
        if (state === STATE.OPEN) {
            setState(STATE.CLOSING);
            setTimeout(() => {
                setState(STATE.CLOSED);
            }, animateTime);
        }
    };

    return {
        state,
        onOpen,
        onClose,
    };
}

export const useDisclosure = () => {
    const [isOpen, setIsOpen] = useState(false);

    const onOpen = () => {
        setIsOpen(true);
    };

    const onClose = () => {
        setIsOpen(false);
    };

    return {
        isOpen,
        onOpen,
        onClose,
    };
};